.hint {
  position: absolute;
  display: flex;
  background-color: #fff;
  padding: 15px;
  border-radius: 30px;
  min-width: 40px;
  max-width: 220px;
  min-height: 40px;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 1.5em;
  color: rgba(#4d4d4d, 0.5);
  box-shadow: 1px 1px 15px 1px rgb(77 77 77 / 5%);

  &::before {
    width: 32px;
    height: 32px;
    top: -11px;
    left: 28px;
    box-shadow: -50px 30px 0 -12px #fff;
  }

  &::after {
    bottom: -22px;
    right: 26px;
    width: 30px;
    height: 30px;
    box-shadow: 40px -34px 0 0 #fff,
                -28px -6px 0 -2px #fff,
                -24px 17px 0 -6px #fff,
                -5px 25px 0 -10px #fff;
    
  }

  &::before,
  &::after {
    content:"";
    background-color: #fff;
    border-radius: 50%;
    display: block;
    position: absolute;
  }

  &--left {
    top: 100px;
    left: 20%;
  }

  &--right {
    bottom: 15px;
    right: 100px;
  }

  @media screen and (max-width: 1024px) {
    &--left {
      left: 20px;
    }
  
    &--right {
      right: 20px;
    }
  }

  @media screen and (max-width: 640px) {
    display: none;
  }
}